
import GetCategoryQuery from "@/graphql/GetCategory"
import AddToCart from "@/components/catalog/AddToCart.vue"
import { useQuery } from "@urql/vue"
import { defineComponent, ref, watch } from "vue"
import { useI18n } from "vue-i18n"
import { useRouter } from "vue-router"
import Pagination from "@/components/Pagination.vue"

export default defineComponent({
    components: {
        AddToCart,
        Pagination
    },
    setup() {
        const { t } = useI18n()
        const router = useRouter()
        const slug = ref(router.currentRoute.value.params.slug)
        const page = ref(1)

        const result = useQuery({
        query: GetCategoryQuery,
            variables: { slug: slug, page: page },
        })

        watch(router.currentRoute, (newValue) => {
            slug.value = newValue.params.slug
            
            result.executeQuery({
                variables: { slug: newValue.params.slug, page: page },
                requestPolicy: "network-only",
            })
        })
        const nextButtonOnClick = () => {
            page.value += 1
            console.log('page ' +  page.value)
            result.executeQuery({
                variables: { slug: slug, page: page },
                requestPolicy: "network-only",
            })
        }
        const previousButtonOnClick = () => {
            
            page.value -= 1
            if (page.value < 0) {
                page.value = 0
            }
            console.log('page ' +  page.value)
            result.executeQuery({
                variables: { slug: slug, page: page },
                requestPolicy: "network-only",
            })
        }
        return {
            t,
            nextButtonOnClick,
            previousButtonOnClick,
            slug,
            fetching: result.fetching,
            data: result.data,
            error: result.error,
        }
    },
});
